*{
  box-sizing: border-box;
}

@font-face {
  font-family: 'Minisystem';
  src: url('./fonts/minisystem.ttf') format('truetype');
}

body{
  background: #2a3347;
  color: #e0d7d7;
}

.App {
  background: #3f4c6b;
  text-align: center;
  width: 850px;
  margin: 20px auto;
  border-top: 20px solid #22293a;
  /* border: 2px solid #e0d7d7; */
  /* border-radius:20px; */
  padding: 25px;
  display: block;
  box-shadow: 0px 10px 50px rgba(0,0,0,.3);
}


.transport-controls-wrapper{
  width:50%;
  display: inline-block;
}
.transport-controls-wrapper span{
  display: inline-block;
  font-family: 'Minisystem';
  font-size: 24px;
  background-color: rgba(0,0,0,.8);
  padding: 10px;
  width: 150px;
  text-align: right;
}
.stop,
.play{
  appearance: none;
  border: 2px solid #e0d7d7;
  appearance: none;
  height: 50px;
  width: 75px;
  border-radius: 3px;
  background-color: #606c88;
  color: #e0d7d7;
  text-transform: uppercase;
  margin: 0 5px;
  font-weight:bolder;
  font-size:14px;
}

.play[data-active=true]{
  background: #e0d7d7;
  color: #3f4c6b;
}

.stop:active,
.play:active{
  opacity: .7;
}

.clearcontrols{
  display: inline-block;
  width: 100%;
  height: 50px;
  color: #fff;
  font-weight: 700;
}
.clearcontrols button{
  margin: 5px;
  width: 150px;
  background-color: #101010;
  color:#fff;
  text-transform: uppercase;
  padding: 5px;
  font-weight: 700;
  border: 2px solid #fff;
  border-radius: 2px;
}

.trackselector-wrapper button[data-selected=true]{
  background-color: #e0d7d7;
  color: #606c88;
  transition: background-color .1s, color .1s; 
}
.trackselector-wrapper button[data-muted=true]{
  background-color: #101010;
  color: #8f8b8b;
  border-color: #8f8b8b;
}
.volumeslider{
  display:inline-block;
  width:20px;
  height: 80px;
  padding: 0;
  margin: 0 2.5px;
}
.volumeslider input{
  width: 80px;
  height: 20px;
  margin: 0;
  transform-origin: 40px 40px;
  transform: rotate(-90deg);
}

.sequencer--button{
  position:relative;
  height:70px;
  width:40px;
  border:1px solid #000;
  display:inline-block;
  margin: 0 5px;
  font-size: 24px;
  line-height:70px;
  border-radius:2px;
  background: linear-gradient(to bottom, #606c88 0%,#3f4c6b 100%); 
  cursor: pointer;
}

.sequencer--button.active{
  background: linear-gradient(to bottom, #b6bac4 0%,#6e7177 100%);
  box-shadow: 0 0 10px rgba(255,255,255,.3);
}

.sequencer--button:active{
  opacity:.7;
}

.sequencer--button.chase,
.sequencer--button.active.chase{
  background: linear-gradient(to bottom, #ec3a3a 0%,#7d1a1a 100%);
}

